import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import styles from './ProjectIntro.module.scss';
import BorderedCard from '../../UI/BorderedCard/BorderedCard';

const ProjectIntro = (props) => {
  const { item, className } = props;
  return (
    <div className={classnames(styles.ProjectIntro, className)}>
      <BorderedCard className={styles.introCard}>
        <img src={item.description.logoUrl} alt="logo" className={styles.logo} />
        <div className={styles.textWrap}>
          <h2>{item.projectName}</h2>
          <p>{item.description.projectDesc}</p>
          <div className={styles.linkWrap}>
            {item.description && item.description.website ? (
              <a href={`https://${item.website}`} target="_blank" rel="noopener noreferrer">
                <span>
                  <img src="/static/images/projectDetail/projectIntro/website.svg" alt="logo" />{' '}
                  {item.description.website}
                </span>
              </a>
            ) : null}

            {item.description && item.description.discord ? (
              <a href={item.description.discord} target="_blank" rel="noopener noreferrer">
                <span>
                  <img src="/static/images/projectDetail/projectIntro/discord.svg" alt="logo" />{' '}
                  Discord
                </span>
              </a>
            ) : null}

            {item.description && item.description.twitter ? (
              <a
                href={`https://twitter.com/${item.twitter}`}
                target="_blank"
                rel="noopener noreferrer">
                <span>
                  <img src="/static/images/projectDetail/projectIntro/twitter.svg" alt="logo" />
                  {`@${item.description.twitter}`}
                </span>
              </a>
            ) : null}
            {item.description && item.description.telegram ? (
              <a href={`https://t.me/${item.telegram}`} target="_blank" rel="noopener noreferrer">
                <span>
                  <img src="/static/images/projectDetail/projectIntro/telegram.svg" alt="logo" />{' '}
                  {`@${item.description.telegram}`}
                </span>
              </a>
            ) : null}
          </div>
        </div>
      </BorderedCard>
    </div>
  );
};

ProjectIntro.propTypes = {
  item: PropTypes.objectOf(PropTypes.any).isRequired,
  className: PropTypes.string,
};

ProjectIntro.defaultProps = { className: '' };

export default ProjectIntro;
